.homeSlides {
  padding: 20px;
    font-size: 2em;
    display: block;
    margin-bottom: 400px;
    margin-right: auto;
    margin-left: auto;
}

.homeCard {
  padding: 10px;
    font-size: 2em;
    display: block;
    margin-top: 10px;
    margin-right: auto;
    margin-left: auto;
}

span.sr-only{
  display:none;
}

.my-tooltip {
  width: 300px;
}
.my-tooltip > .tooltip-inner {
  background-color:  #083d8d;
  font-size: 1.5em;
  }


.carousel-control-next-icon, .carousel-control-prev-icon {
  background-color: #00c;
  display: none;
}


element.style {
}
div.slides > a.carousel-control-prev > span.carousel-control-prev-icon {
    padding: 10px;
    color: #6F69AC;
}

  
div.rhap_container.audio-slides  {
  background-color: #083d8d;
    border-radius: 10px 0px 0px 0px;
    width: 400px;
}

div.rhap_progress-filled,
div.rhap_progress-indicator{
  background: #fff;
}

button.rhap_main-controls-button{
  color:#fff;
}

p1, p2, p3, p4, p5, p6{
  display: block;
  margin: 7px 100px 5px 100px;
  padding: 9px;
  font-weight: 900;
  color: #fff;
  position:relative;
}

p1{ background: #6F69AC;}
p2{background: #ef476f;}
p3{background: #06d6a0;}
p4{background: #118ab2;}
p5{background: #073b4c;}
p6{background: #c1121f;}

p1:before {
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  border-left: 10px solid transparent;
  border-right: 10px solid #6F69AC;
  border-top: 10px solid #6F69AC;
  border-bottom: 10px solid transparent;
  left: -19px;
  top: 6px;
}

