div.slides > a.carousel-control-prev > span.carousel-control-prev-icon{
  padding: 10px;
  color: #6F69AC;
}

.slides > ol.carousel-indicators > li.hover{
  opacity: 0.5;
  transform: scale(1.05);     /* Equal to scaleX(2) scaleY(2) */
   transition-duration: 2s;
}

.slides > ol.carousel-indicators > li.active{
  background-color: #fff ;
}
.slides > ol.carousel-indicators > li{
  background-color: rgb(13, 202, 240);
  padding: 20px;
  margin: 4px;
  display: inline-block;
  border-radius: 50%;
}

.slides > ol.carousel-indicators{
    left: 400px;
    bottom: 416px;
    background-color: #083d8d;
    display: block;
    right: 10px;
    padding: 0px;
    margin: 0px;
    /* border-radius: 15px; */
    height: 60px;
    display: inline-flex;
}


.text-slides {
  position: absolute;
  top: 60px;
  left: 30%;
  right: 0px;
  background-color: rgb(247, 245, 244);
  height: 400px;
}

.img-slides {
  height: 400px;
  background-color: rgb(247, 245, 244);
  padding: 70px;
}

.list-button {
  height: 110px;
  font-size: 2em;
  text-decoration: none;
  cursor: pointer;
  padding-top: 25px;
}

.list-button > a {
  text-decoration: none;
}

.list-button:hover {
  opacity: 0.5;
 transform: scale(1.05);     /* Equal to scaleX(2) scaleY(2) */
  transition-duration: 2s;
 /*transform: scale(0.6);*/
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
